___CSS_LOADER_EXPORT___.push([module.id, `.create-assignment-container {
  display: flex;
}

.create-assignment-content {
  width: 80%;
  margin-left: 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 2px solid #ccc;
  box-shadow: 0px 3px 6px #B4B3B329;
  height: 15%;
  margin-top: 25px;
} 

.header h4 {
  margin-top:50px;
}

.header button {
  margin-left: 10px;
  margin-top:50px;
}
.assignment-form {
  max-width: 600px;
  margin: 20px auto;
}

.assignment-form label {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 8px;
}

.assignment-form select,
.assignment-form input {
  width: 100%;
  padding: 8px;
  margin-bottom: 16px;
  border: 2px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  border-radius: 10px;
}

.form-buttons {
  display: flex;
  justify-content: space-between;
}
.custom-hover{
  cursor: pointer;
}
