.reportSection {
    padding: 20px;
  }
  
  .d-flex.align-items-center h4 {
    font-size: 24px;
    margin-right: 10px;
  }
  
  .d-flex.justify-content-end .btn {
    margin-left: 10px;
    font-size: 16px;
  }
  
  .question-card {
    background-color: #fff;
